<template>
    <views class="shop-detail">
        <nav-bar
            title="门店详情"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <views class="shop-detail-header">
            <images :src="headImg"></images>
            <views class="shop-detail-header-body">
                <views class="title">{{ detail.name }}</views>
                <views class="tag">
                    <views class="phone">
                        <images :src="phoneIcon"></images>
                        <a :href="tel(detail.contact_phone)" @click.stop>{{ detail.contact_phone }}</a>
                    </views>
                    <views class="addr" @click="handleAddr">
                        <images :src="addrIcon"></images>
                    </views>
                </views>
            </views>
        </views>
        <views class="line"></views>
        <views class="shop-detail-intro">
            <views class="shop-detail-intro-title">门店介绍</views>
            <views class="shop-detail-intro-body" v-html="detail.detail_body"></views>
        </views>
        <views class="line"></views>
        <views class="shop-detail-imgs">
            <views class="shop-detail-imgs-title">门店图片</views>
            <views class="shop-detail-imgs-body">
                <images :src="shopImg"></images>
                <images :src="shopImg"></images>
                <images :src="shopImg"></images>
            </views>
        </views>
        <views class="line"></views>
        <views class="shop-detail-map">
            <views class="shop-detail-map-title">门店地址</views>
            <views class="shop-detail-map-body">
                <el-amap class="amap-box" :vid="'shop-detail'" :center="[113.400929,23.055819]" :zoom="14"></el-amap>
            </views>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    components: {
        NavBar
    },
    mixins: [tools],
    data() {
        return {
            detail: {},
            phoneIcon: require('@/assets/images/phone_icon.png'),
            addrIcon: require('@/assets/images/detail_addr.png'),
            headImg: require('@/assets/images/shop_detail_header.png'),
            shopImg: require('@/assets/images/shop.png')
        }
    },
    created() {
        this.getShopDetail()
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        tel(str) {
            return `tel:${str}`
        },
        handleAddr() {
            this.push('/map')
        },
        getShopDetail() {
            api.getShopDetail().then((res) => {
                console.log(res)
                this.detail = res.data
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
